article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

a {
  &:focus {
    outline: thin dotted;
  }

  &:active,
  &:hover {
    outline: 0;
  }
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}

mark {
  background: #ff0;
  color: #000;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
}

q {
  quotes: '\201C''\201D''\2018''\2019';
}

small {
  font-size: 80%;
}

sub {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

button,
select {
  text-transform: none;
}

button,
html input[type='button'] {
  -webkit-appearance: button;
  cursor: pointer;
}

input {
  &[type='reset'],
  &[type='submit'] {
    -webkit-appearance: button;
    cursor: pointer;
  }
}

button[disabled],
html input[disabled] {
  cursor: default;
}

input {
  &[type='checkbox'],
  &[type='radio'] {
    box-sizing: border-box;
    padding: 0;
  }

  &[type='search'] {
    -webkit-appearance: textfield;
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;

    &::-webkit-search-cancel-button,
    &::-webkit-search-decoration {
      -webkit-appearance: none;
    }
  }
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;

  &::after,
  &::before {
    box-sizing: border-box;
  }
}

:root {
  font-size: 16px;
}

html {
  height: 100%;
}

body {
  height: 100%;
  --color-text: #fff;
  --color-bg: #000;
  --color-link: #f0f0f0;
  --color-link-hover: #fff;
  --color-title: #fff;
  --img-maxwidth: none;
  --blendmode-title: overlay;
  --filter-img: none;
  color: var(--color-text);
  background-color: var(--color-bg);
  font-family: quiroh, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Page Loader */

.js .loading {
  &::before {
    content: '';
    position: fixed;
    z-index: 100000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--color-bg);
  }

  &::after {
    content: '';
    position: fixed;
    z-index: 100000;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    margin: -30px 0 0 -30px;
    pointer-events: none;
    border-radius: 50%;
    opacity: 0.4;
    background: var(--color-link);
    animation: loaderAnim 0.7s linear infinite alternate forwards;
  }
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}

a {
  text-decoration: none;
  color: var(--color-link);

  &:hover,
  &:focus {
    color: var(--color-link-hover);
    outline: 0;
  }
}

.content {
  height: 300px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  isolation: isolate;
}

.content__go-back {
  position: absolute;
  top: 0;
  left: 0;
  font-family: forma-djr-display, sans-serif;
  font-weight: 700;
  z-index: 10000;
  font-size: 1.5vw;
  color: var(--color-text);
  mix-blend-mode: soft-light;
  padding: 2vw;
}

.content__title {
  font-family: forma-djr-display, sans-serif;
  font-weight: 700;
  position: relative;
  z-index: 10000;
  font-size: 27vw;
  mix-blend-mode: var(--blendmode-title);
  -webkit-text-stroke: 2px var(--color-title);
  text-stroke: 2px var(--color-title);
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
  color: transparent;
  pointer-events: none;
  margin: 0;
}

.content__description {
  font-family: forma-djr-display, sans-serif;
  font-weight: 700;
  position: relative;
  z-index: 10000;
  font-size: 2vw;
  color: var(--color-text);
  mix-blend-mode: soft-light;
}

.content__img {
  max-width: var(--img-maxwidth);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  will-change: transform;
  filter: var(--filter-img);
}

.content__img--full {
  width: 100%;
  height: 100%;
  background-size: cover;
}

@media screen and (min-width: 53em) {
  :root {
    font-size: 18px;
  }

  body {
    overflow: hidden;
  }

  .message {
    display: none;
  }

  .frame {
    position: fixed;
    text-align: left;
    z-index: 10000;
    top: 0;
    left: 0;
    display: grid;
    align-content: space-between;
    width: 100%;
    max-width: none;
    height: 100vh;
    padding: 2rem;
    pointer-events: none;
    grid-template-columns: 75% 25%;
    grid-template-rows: auto auto auto;
    grid-template-areas: 'pagetitle github' '... ...' 'title ...';
  }

  .frame__pagetitle {
    grid-area: pagetitle;
    margin: 0;
  }

  .frame__title-wrap {
    grid-area: title;
    display: flex;
  }

  .frame__title {
    margin: 0;
    font-weight: normal;
  }

  .frame__links {
    padding: 0;
    margin: 0 0 0 3rem;
    justify-self: end;
  }

  .frame__demos {
    margin: 0 0 0 3rem;
  }

  .frame__github {
    grid-area: github;
    justify-self: end;
  }

  .frame a {
    pointer-events: auto;
  }

  .content {
    height: 100vh;
    overflow: hidden;
  }
}
